@import url(https://fonts.googleapis.com/css2?family=Lato:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
* {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

::-webkit-input-placeholder {
  font-size: 16px;
}

:-ms-input-placeholder {
  font-size: 16px;
}

::-ms-input-placeholder {
  font-size: 16px;
}

::placeholder {
  font-size: 16px;
}
.listItem__wrapper {
  list-style: none;
  padding: 50px 30px;
  display: flex;
  align-items: center;
}

.listItem__image {
  margin-right: 30px;
  flex-shrink: 0;
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.listItem__name {
  margin: 0;
  color: blue;
  font-weight: 700;
}

.listItem__description {
  margin: 10px 0 20px;
  font-weight: 400;
}

.listItem__button {
  font-size: 12px;
  text-decoration: none;
  padding: 7px 12px;
  font-weight: 500;
  background: none;
  border: 2px solid blue;
  color: blue;
}
.listWrapper__wrapper {
  width: 80vw;
  margin: 100px auto 0;
}
.form__wrapper {
  width: 50%;
  margin: 50px auto 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form__wrapper h2 {
  color: blue;
}

.form__input {
  color: #585858;
  font-size: 16px;
  border: none;
  line-height: 22px;
  width: 100%;
  background: none;
  border-bottom: 2px solid #7d7d7d;
  margin-bottom: 10px;
}

.form__textarea {
  min-width: 350px;
  resize: none;
  min-height: 150px;
  color: #585858;
  font-size: 16px;
  border: none;
  line-height: 22px;
  background: none;
  border-bottom: 2px solid #7d7d7d;
}

.form__label {
  color: #7d7d7d;
  position: absolute;
  top: 3px;
  left: 0;
  transition: 0.2s ease-out all;
  font-size: 16px;
}

.form__form-item {
  width: 100%;
  margin: 24px 0;
  position: relative;
  flex-shrink: 0;
}

.form__input:focus,
.form__textarea:focus {
  outline: 1px solid #7d7d7d;
}

.form__input:focus+.form__label,
.form__textarea:focus+.form__label {
  top: -22px;
  font-size: 13px;
}

.form__input:not(:-ms-input-placeholder)+.form__label, .form__textarea:not(:-ms-input-placeholder)+.form__label {
  top: -22px;
  font-size: 13px;
}

.form__input:not(:placeholder-shown)+.form__label,
.form__textarea:not(:placeholder-shown)+.form__label {
  top: -22px;
  font-size: 13px;
}

.form__button {
  font-size: 12px;
  text-decoration: none;
  padding: 7px 12px;
  font-weight: 500;
  background: none;
  border: 2px solid blue;
  color: blue;
}
